import "./Form.scss";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";

const nameRegExp = /[a-zA-z--]$/;
const phoneRegExp = /[+7][0-9]{10}$/;
const mailRegExp = /^[a-zA-z0-9-._]+@[a-z0-9-_]+\.[a-z0-9-_]{2,6}/ui;
const ageRegExp = /^[0-9]{1,3}$/;
// const loginRegExp = /[a-zA-Z0-9-_]$/;
// const passRegExp = /^[a-zA-Z0-9-_]$/;

const schema = yup.object().shape( {
  name: yup
    .string()
    .required( ",   " )
    .matches( nameRegExp, "     " )
    .min( 2, "       2- " )
    .max( 15, "       15- " ),

  message: yup
    .string()
    .required( ",   " )
    .min( 2, "       2- " ),

  phone: yup
    .string()
    .required( ",   " )
    .matches( phoneRegExp, "   : +79172546925" ),

  date: yup
    .string()
    .required( ",   " ),

  age: yup
    .string()
    .required( ",   " )
    .min( 1, "        " )
    .max( 3, "       3- " )
    .matches( ageRegExp, "       " ),

  email: yup
    .string()
    .required( ",   " )
    .matches( mailRegExp, "  email" ),

  consent: yup
    .bool()
    .oneOf( [ true ], ",   " ),

  select: yup
    .string()
    .required( ",   " ),

  // recommend: yup
  //   .string()
  //   .required( ",   " ),


  // number: yup
  // .number()
  // .required( ",   " )
  // .min(1, "        ")
  // .max(3, "       3- ")
  // .positive( "    " )
  // .integer( "   " ),

  // login: yup
  //   .string()
  //   .required( ",   " )
  //   .matches( loginRegExp, "   ,  '-'  '_'" )
  //   .min( 2, "       2- " )
  //   .max( 20, "       20- " ),

  // password: yup
  //   .string()
  //   .required( ",   " )
  //   .matches( passRegExp, "   ,  '-'  '_'" )
  //   .min( 8, "       8- " )
  //   .max( 20, "       20- " ),
  //
  // confirmPassword: yup
  //   .string()
  //   .required( ",   " )
  //   .oneOf( [ yup.ref( "password" ), null ], "        ''" )
} );


function Form() {
  const { register, handleSubmit, reset, setValue, formState: { errors } } = useForm( {
    resolver: yupResolver( schema ),
    mode: "onTouched",
  } );
  const [ isMessageSent, setMessageSent ] = useState( 0 );
  const [ isFirstOption, setFirstOption ] = useState( 1 );
  const [ typeOfDate, setTypeOfDate ] = useState( "text" );

  const onSubmit = (data) => {
    console.log( data );
    setMessageSent( 1 );
    setTimeout( () => {
      setMessageSent( 0 );
      reset();
      setFirstOption( 1 );
      setTypeOfDate( "text" );
    }, 3000 );
  };

  const onError = (errors, e) => console.log( errors, e );

  const handleFocus = (e) => {
    setTypeOfDate( "date" );
  };


  return (
    <>
      <form action="#" className="form" onSubmit={ handleSubmit( onSubmit, onError ) }>
        <h1 className="form__title"> ,    <br/>react-hook-form  yup</h1>
        <h2 className="form__subtitle">  </h2>
        <fieldset className="form__fieldset">
          <legend className="form__legend"> </legend>
          <p className="form__item"><label className="form__label" htmlFor="name"></label>
            <input
              type="text"
              className={ errors.name ? "form__input form__input--error" : "form__input" }
              name="name"
              id="name"
              placeholder=""
              { ...register( "name" ) }/>
          </p>
          { errors.name && <p className="form--error">{ errors.name.message }</p> }
          <p className="form__item"><label className="form__label" htmlFor="phone"></label>
            <input
              type="text"
              className={ errors.phone ? "form__input form__input--error" : "form__input" }
              name="phone"
              id="phone"
              placeholder="+79275681452"
              { ...register( "phone" ) }/>
          </p>
          { errors.phone && <p className="form--error">{ errors.phone.message }</p> }
          <p className="form__item"><label className="form__label" htmlFor="email">. </label>
            <input
              type="text"
              className={ errors.email ? "form__input form__input--error" : "form__input" }
              name="email"
              id="email"
              placeholder="mail@mail.ru"
              { ...register( "email" ) }/>
          </p>
          { errors.email && <p className="form--error">{ errors.email.message }</p> }
          <p className="form__item"><label className="form__label" htmlFor="date"> </label>
            <input
              // type="text"
              type={ typeOfDate }
              placeholder="//"
              className={ errors.date ? "form__input form__input--error" : "form__input" }
              name="date"
              id="date"
              { ...register( "date" ) }
              onFocus={ handleFocus }
            />
          </p>
          { errors.date && <p className="form--error">{ errors.date.message }</p> }
          <p className="form__item form__item--position--left">
            <input className={ errors.consent ? "form__check form__input--error" : "form__check" }
                   type="checkbox"
                   name="consent"
                   id="consent"
                   { ...register( "consent" ) }
            />
            <label className="form__label form__label--wide" htmlFor="consent">    
              
              </label>
          </p>
          { errors.consent && <p className="form--error">{ errors.consent.message }</p> }
        </fieldset>

        <fieldset className="form__fieldset">
          <legend className="form__legend"> </legend>
          <p className="form__item">
            <label className="form__label" htmlFor="age"></label>
            <input type="number"
                   placeholder="18"
                   className={ errors.age ? "form__input form__input--error" : "form__input" }
                   name="age"
                   id="age"
                   { ...register( "age" ) }
            />
          </p>
          { errors.age && <p className="form--error">{ errors.age.message }</p> }
          <p className="form__item form__item--position--relative">

            <label className="form__label" htmlFor="cuisine"> </label>
            { isFirstOption ?
              <span className="form__option-first"
                // style={ style }
              >-    -</span> : null }
            <select
              className={ errors.cuisine ? "form__select form__select--error" : "form__select" }
              placeholder=" "
              name="cuisine"
              id="cuisine"
              { ...register( "select" ) }

              onChange={ (e) => {
                setValue( "select", e.target.value, { shouldValidate: true } );
                setFirstOption( 0 );
              } }
            >
              <option className="form__option" value=" " hidden></option>
              <option className="form__option" value="russian"></option>
              <option className="form__option" value="japanese"></option>
              <option className="form__option" value="italian"></option>
              <option className="form__option" value="alia"></option>
            </select>
          </p>
          { errors.cuisine && <p className="form--error">{ errors.cuisine.message }</p> }
          <p className="form__item">
            <label className="form__label" htmlFor="message">       ?</label>
            <textarea
              placeholder="  "
              className="form__mess"
              name="message"
              id="message"
              { ...register( "message" ) }
            />
          </p>
          { errors.message && <p className="form--error">{ errors.message.message }</p> }
        </fieldset>
        <fieldset className="form__fieldset">
          <legend className="form__legend">  </legend>
          <p className="form__question">    ?</p>

          <p className="form__item form__item--position--left form__item--position--left">
            <input
              className="form__check"
              type="checkbox"
              name="near"
              id="near"
              { ...register( "near" ) }
            />
            <label className="form__label form__label--wide" htmlFor="near">  /</label>
          </p>
          <p className="form__item form__item--position--left">
            <input
              className="form__check"
              type="checkbox"
              name="advertising"
              id="advertising"
              { ...register( "advertising" ) }
            />
            <label className="form__label form__label--wide" htmlFor="advertising"> </label>
          </p>
          <p className="form__item form__item--position--left">
            <input
              className="form__check"
              type="checkbox"
              name="advice"
              id="advice"
              { ...register( "advice" ) }
            />
            <label className="form__label form__label--wide" htmlFor="advice"></label>
          </p>
          <p className="form__item form__item--position--left">
            <input
              className="form__check"
              type="checkbox"
              name="quality"
              id="quality"
              { ...register( "quality" ) }
            />
            <label className="form__label form__label--wide" htmlFor="quality"> 
              /</label>
          </p>

          <p className="form__question">      ?</p>
          <p className="form__item form__item--position--left form__item--position--left">
            <input
              className="form__check"
              type="radio"
              name="recommend"
              value="true"
              id="true"
              // checked="checked"
              { ...register( "recommend" ) }
            />
            <label className="form__label form__label--wide" htmlFor="true"></label>
          </p>
          <p className="form__item form__item--position--left">
            <input
              className="form__check"
              type="radio"
              name="recommend"
              value="false"
              id="false"
              { ...register( "recommend" ) }
            />
            <label className="form__label form__label--wide" htmlFor="false"></label>
          </p>
          <p className="form__item form__item--position--left">
            <input
              className="form__check"
              type="radio"
              name="recommend"
              value="don't know"
              id="don't know"
              { ...register( "recommend" ) }
            />
            <label className="form__label form__label--wide" htmlFor="don't know"> </label>
          </p>
        </fieldset>
        { isMessageSent ? <p className="form__success">  </p> : null }

        <button type="submit" className="form__btn"></button>
      </form>

    </>
  );
}

export default Form;